import Vue from 'vue'
import App from './App.vue'
import VueRouter from "vue-router"

Vue.use(VueRouter)
const router = new VueRouter({
  mode:"history",
  routes: [
    {
        path:'/',
        components: require ('./components/pages/accueil.vue'),
        name:"accueil",
        meta : {
            requiresAuth:true,
            reload: true
        },
    },

    {
      path:'/1',
      components: require ('./components/pages/projet1.vue'),
      name:"projet1",
      meta : {
          requiresAuth:true
      }
  }
],

})

new Vue({
  el: '#app',
  router,
  render: h => h(App),
})



